import { UserAction } from "../actions/userAction";
import EActionTypes from "../actions/actionTypes";

// user info
export interface UserData {
  id: number;
  name: string;
  username: string;
  email: string;
  address: {
    street: string;
    suite: string;
    city: string;
    zipcode: string;
    geo: {
      lat: string;
      lng: string;
    };
  };
  phone: string;
  website: string;
  company: {
    name: string;
    catchPhrase: string;
    bs: string;
  };
}

export interface IUserState {
  users: UserData[];
  user?: UserData;
}

const initialState: IUserState = {
  users: [],
  user: undefined
};

const userReducer = (state: IUserState = initialState, action: UserAction) => {
  switch (action.type) {
    case EActionTypes.GET_USERS:
      return { ...state, users: action.payload };
    case EActionTypes.GET_USER:
      return { ...state, user: action.payload };
    default:
      return state;
  }
};

export default userReducer;
