import React from "react";
import gameloft from '../../assets/images/gameloft.png';
import './GameloftVillage.scss';

const GameloftVillage: React.FC = () => {
  

  return (
    <div className="gameloft-villiage">
      <img src={gameloft} alt="gameloft"/>
      <span>VILLAGE</span>
    </div>
  );
};

export default GameloftVillage;
