import React from "react";
import "./Overlay.scss";
import { useSelector, useDispatch } from "react-redux";
import { StoreState } from "../../redux/reducers";
import FoodOrder from "../../assets/images/food-order.png";
import { canteenOder } from "../../redux/actions/canteenAction";

const Overlay: React.SFC = () => {
  const order = useSelector((state: StoreState) => state.canteen);
  const dispatch = useDispatch();

  const orderNow = () => {
    dispatch(canteenOder(''));
  }

  return (
    <div
      className="overlay"
      style={order.food !== "" ? { display: "flex" } : { display: "none" }}
    >
      <div className="order">
        <div className="food">
          <img src={FoodOrder} alt="" />
          <div className="food-info">
            <div className="name-container">
              <p className="name">Spring Rolls with Vegetables </p>
              <p className="price">30,000</p>
            </div>
            <textarea name="" id="" rows={4} placeholder="Special request"></textarea>
            <div className="matcher">
              <input type="checkbox" />
              Lunch Matcher
            </div>
          </div>
        </div>
        <div className="order-button" onClick={orderNow}>Order Now</div>
      </div>
    </div>
  );
};

export default Overlay;
