import React from "react";
import { Link } from "react-router-dom";
import { ReactComponent as ClubIcon } from "../../../assets/images/weight-app.svg";
import { ReactComponent as CalendarIcon } from "../../../assets/images/calendar-app.svg";
import { ReactComponent as OfficeNavigatorIcon } from "../../../assets/images/maps-app.svg";
import { ReactComponent as CanteenIcon } from "../../../assets/images/fruit-app.svg";
import { ReactComponent as TreeIcon } from "../../../assets/images/tree-gross-outline-app.svg";
import { ReactComponent as GamesIcon } from "../../../assets/images/console.svg";
import "../appList/AppList.scss";

const Apps = [
  {
    name: "club-icon",
    displayName: "Clubs",
    href: "/clubs",
    svg: <ClubIcon />
  },
  {
    name: "meeting-icon",
    displayName: "Meeting",
    href: "#",
    svg: <CalendarIcon />
  },
  {
    name: "map-icon",
    displayName: "Office Tracker",
    href: "/officetracker",
    svg: <OfficeNavigatorIcon />
  },
  {
    name: "canteen-icon",
    displayName: "Canteen",
    href: "#",
    svg: <CanteenIcon />
  },
  {
    name: "green-icon",
    displayName: "Go Green",
    href: "/gogreen",
    svg: <TreeIcon />
  },
  {
    name: "game-icon",
    displayName: "Our Games",
    href: "#",
    svg: <GamesIcon />
  }
];

const AppList: React.FC = () => {
  return (
    <div className="app-list">
      <ul>
        {Apps.map(e => (
          <li key={e.name}>
            <Link to={e.href}>
              <div className={e.name}>{e.svg}</div>
              <span>{e.displayName}</span>
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default AppList;
