import React from "react";
import "../gogreen/GoGreen.scss";
import Footer from "../../components/dashboard/footer/Footer";
import Header from "../../components/dashboard/header/Header";
import GoGreenImg from "../../assets/images/go-green.png";

const Clubs: React.SFC = () => {
  return (
    <div className="go-green">
      <Header></Header>
      <div
        className="go-green-img"
        style={{ backgroundImage: "url(" + GoGreenImg + ")" }}
      ></div>
      <Footer></Footer>
    </div>
  );
};

export default Clubs;
