import React, { useEffect } from "react";
import "../clubs/Clubs.scss";
import Footer from "../../components/dashboard/footer/Footer";
import Header from "../../components/dashboard/header/Header";
import ClubNews from "../../assets/images/club-img-1.png";
import ClubNews1 from "../../assets/images/news.png";

const Clubs: React.SFC = () => {
  useEffect(() => {
    ($("#join-carousel") as any).slick();
  }, []);

  return (
    <div className="clubs">
      <Header></Header>
      <p>Join Now!</p>
      <div
        className="join-carousel"
        data-slick='{"slidesToShow": 1, "slidesToScroll": 1, "arrows": false, "centerMode": true, "variableWidth": true}'
        id="join-carousel"
      >
        <img className="item" src={ClubNews} alt="" />
        <img className="item" src={ClubNews} alt="" />
        <img className="item" src={ClubNews} alt="" />
        <img className="item" src={ClubNews} alt="" />
      </div>
      <p>Events</p>
      <div className="event-carousel">
        <img src={ClubNews1} alt="" />
      </div>
      <Footer></Footer>
    </div>
  );
};

export default Clubs;
