import EActionTypes, { IBaseAction } from "./actionTypes";
import { ActionCreator } from "redux";

export interface ICanteenOrderAction extends IBaseAction {
  type: EActionTypes.CANTEEN_ORDER;
  payload: string
}

export const canteenOder: ActionCreator<ICanteenOrderAction> = (food) => {
  return {
    type: EActionTypes.CANTEEN_ORDER,
    payload: food
  };
};

export type CanteenAction = ICanteenOrderAction;
