import React from "react";
import DefaultIcon from "../../../assets/images/default_image.png";
import './Header.scss';
import GameloftVillage from "../../gameloftVillage/GameloftVillage";


const Header: React.FC = () => {
  

  return (
    <div className="header">
      <GameloftVillage></GameloftVillage>
      <div className="user">
        <span className="hello">Hello USER!</span>
        <img src={DefaultIcon} alt="gameloft-logo"></img>
      </div>
      
    </div>
  );
};

export default Header;
