import React, { useEffect } from "react";
import "./News.scss";
import New1 from '../../../assets/images/news/1.png';
import New2 from '../../../assets/images/news/2.png';
import New3 from '../../../assets/images/news/3.png';

const News: React.FC = () => {
  useEffect(() => {
    ($("#new-carosel") as any).slick();
  }, []);

  return (
    <div className="news">
      <div className="title">News</div>
      <div
        className="carosel"
        data-slick='{"slidesToShow": 1, "slidesToScroll": 1, "arrows": false, "centerMode": true, "variableWidth": true}'
        id="new-carosel"
      >
        <div className="item">
          <img src={New1} alt="" />
        </div>
        <div className="item">
          <img src={New2} alt="" />
        </div>
        <div className="item">
          <img src={New3} alt="" />
        </div>
        <div className="item">
          <img src={New1} alt="" />
        </div>
      </div>
    </div>
  );
};

export default News;
