import EActionTypes, { IBaseAction } from "./actionTypes";
import { useSelector, useDispatch } from "react-redux";
import { StoreState } from "../reducers";
import { IErrorState } from "../reducers/errorReducer";

export interface IErrorAction extends IBaseAction {
  type: EActionTypes.ERROR;
  error: IErrorState;
}

export interface IErrorClearAction extends IBaseAction {
  type: EActionTypes.ERROR_CLEAR;
}

export enum EErrorType {
  NONE = "NONE",
  CONNECTION_ERROR = "CONNECTION_ERROR",
  APP_ERROR = "APP_ERROR"
}

// get out the error if there have then clear it
export const useCheckError = () => {
  const error = { ...useSelector((state: StoreState) => state.error) };
  const dispatch = useDispatch();
  if (error.errorType !== EErrorType.NONE) {
    dispatch({
      type: EActionTypes.ERROR_CLEAR
    });
    return error;
  }
  return null;
};

export type ErrorAction = IErrorAction | IErrorClearAction;
