import React from "react";
import "./Home.scss";
import DefaultIcon from "../../assets/images/default_image.png";
import Plus from "../../assets/images/plus.png";
import { Link } from "react-router-dom";
import GameloftVillage from "../../components/gameloftVillage/GameloftVillage";

const Home: React.SFC = () => {
  return (
    <div className="home">
      <div className="user-icon">
        <img src={DefaultIcon} alt="icon" className="avatar" />
        <img src={Plus} alt="plus" className="plus" />
      </div>
      <div className="welcome">
        <p>Welcome to</p>
        <GameloftVillage></GameloftVillage>
      </div>
      <div className="messages">
        <p>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
          eiusmod tempor incididunt ut labore et dolore magna aliqua.
        </p>
      </div>
      <Link className="btn-next" to="/dashboard">
        Next
      </Link>
    </div>
  );
};

export default Home;
