import React from "react";
import "./App.scss";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Login from "../../views/login/Login";
import DashBoard from "../../views/dashBoard/DashBoard";
import Home from "../../views/home/Home";
import Overlay from "../overlay/Overlay";
import Clubs from "../../views/clubs/Clubs";
import OfficeTracker from "../../views/officetracker/OfficeTracker";
import GoGreen from "../../views/gogreen/GoGreen";

const App: React.FC = () => {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div className="app">
      <Overlay></Overlay>
        <Switch>
          <Route path="/dashboard">
            <DashBoard></DashBoard>
          </Route>
          <Route path="/home">
            <Home></Home>
          </Route>
          <Route path="/clubs">
            <Clubs />
          </Route>
          <Route path="/officetracker">
            <OfficeTracker />
          </Route>
          <Route path="/gogreen">
            <GoGreen />
          </Route>
          <Route path="/">
            <Login></Login>
          </Route>
        </Switch>        
      </div>
    </Router>
  );
};

export default App;
