import EActionTypes from "../actions/actionTypes"
import { CanteenAction } from "../actions/canteenAction";

export interface ICanteenState {
    food: string
}

const initialState: ICanteenState = {
    food: ''
};

export const canteenReducer = (state = initialState, action: CanteenAction): ICanteenState => {
    switch(action.type) {
        case EActionTypes.CANTEEN_ORDER:
            return {...state, food: action.payload};
        default:
            return state;
    }
}