import React, { useEffect } from "react";
import "./CanteenMenu.scss";

import Food1 from "../../../assets/images/canteen/1.png";
import Food2 from "../../../assets/images/canteen/2.png";
import Food3 from "../../../assets/images/canteen/3.png";
import Food4 from "../../../assets/images/canteen/4.png";
import { useDispatch } from "react-redux";
import { canteenOder } from "../../../redux/actions/canteenAction";

const CanteenMenu: React.FC = () => {

  const dispatch = useDispatch();
  
  useEffect(() => {
    ($("#canteen-carosel") as any).slick();
  }, []);

  const order = () =>  {
    dispatch(canteenOder('food'));
  }

  return (
    <div className="canteen-menu">
      <div className="title">Today's Menu</div>
      <div
        className="carosel"
        data-slick='{"slidesToShow": 4, "slidesToScroll": 2, "arrows": false, "centerMode": false, "infinite": false, "variableWidth": true}'
        id="canteen-carosel"
      >
        <div className="item" onClick={order}>
          <img src={Food1} alt="" />
        </div>
        <div className="item" onClick={order}>
          <img src={Food2} alt="" />
        </div>
        <div className="item" onClick={order}>
          <img src={Food3} alt="" />
        </div>
        <div className="item" onClick={order}>
          <img src={Food4} alt="" />
        </div>
        <div className="item" onClick={order}>
          <img src={Food1} alt="" />
        </div>
        <div className="item" onClick={order}>
          <img src={Food2} alt="" />
        </div>
      </div>
    </div>
  );
};

export default CanteenMenu;
