import React from "react";
import { Link } from "react-router-dom";
import "../footer/Footer.scss";
import { ReactComponent as BellIcon } from "../../../assets/images/bell.svg";
import { ReactComponent as CalendarIcon } from "../../../assets/images/calendar.svg";
import { ReactComponent as CogIcon } from "../../../assets/images/cog.svg";
import { ReactComponent as HomeIcon } from "../../../assets/images/home.svg";
import { ReactComponent as LogOutIcon } from "../../../assets/images/log-out.svg";

const Footer: React.FC = () => {
  return (
    <div className="footer">
      <ul>
        <li>
          <Link className="icon-home" to="/dashboard">
            <HomeIcon />
          </Link>
        </li>
        <li>
          <Link className="icon-noties" to="#">
            <div className="noti-count">3</div>
            <BellIcon />
          </Link>
        </li>
        <li>
          <Link className="icon-calendar" to="#">
            <CalendarIcon />
          </Link>
        </li>
        <li>
          <Link className="icon-setting" to="#">
            <CogIcon />
          </Link>
        </li>
        <li>
          <Link className="icon-signout" to="#">
            <LogOutIcon />
          </Link>
        </li>
      </ul>
    </div>
  );
};

export default Footer;
