import { ErrorAction, EErrorType } from "../actions/errorAction"
import EActionTypes from "../actions/actionTypes"

export interface IErrorState {
    errorType: EErrorType,
    errorCode?: number,
    detail?: any,
}

const initialState: IErrorState = {
    errorType: EErrorType.NONE
};

export const errorReducer = (state = initialState, action: ErrorAction): IErrorState => {
    switch(action.type) {
        case EActionTypes.ERROR:
            return action.error;
        case EActionTypes.ERROR_CLEAR:
            state.errorType = EErrorType.NONE;       // NOITCE: i use mutable here so clear error will not trigger re-render 
            return state;
        default:
            return state;
    }
}